@use "./variables.scss" as *;
@use "../mixins.scss" as *;

.advantages {
  background-color: $color-card-secondary-background;
}

.advantages__container {
  margin-right: 18px;
  margin-left: 18px;
}

.advantages__list {
  @include list-reset;
}

.advantages__item {
  padding-top: 24px;
  padding-bottom: 24px;

  &:not(:last-child) {
    border-bottom: 1px solid $color-card-secondary-border;
  }

  &:last-child {
    padding-bottom: 28px;
  }
}

@media (min-width: $tablet-width) {
  .advantages__container {
    margin: 0;
  }

  .advantages__list {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
  }

  .advantages__item {
    padding: 41px 33px;

    &:not(:last-child) {
      border-bottom: none;
    }
  }
}

@media (min-width: $desktop-width) {
  .advantages__item {
    padding: 52px 50px 50px;
  }
}
