@font-face {
  font-family: "PT Sans";
  font-style: normal;
  font-weight: 400;
  src: url("../fonts/ptsans.woff2") format("woff2");
  src: url("../fonts/ptsans.woff") format("woff");
  font-display: swap;
}
@font-face {
  font-family: "PT Sans";
  font-style: normal;
  font-weight: 700;
  src: url("../fonts/ptsansbold.woff2") format("woff2");
  src: url("../fonts/ptsansbold.woff") format("woff");
  font-display: swap;
}
html,
body {
  margin: 0;
}

a {
  text-decoration: none;
}

*,
*::before,
*::after {
  box-sizing: border-box;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  border: 0;
  clip: rect(0 0 0 0);
  overflow: hidden;
}

.page {
  background-color: #e7e5e5;
}

.page__body {
  max-width: 1200px;
  margin: 0 auto;
  background-color: #ffffff;
  font-family: "PT Sans", "Arial", sans-serif;
  font-size: 14px;
  font-weight: 400;
  line-height: 21px;
  text-transform: uppercase;
  text-align: center;
}

.header {
  background-color: #ffffff;
  display: flex;
  flex-direction: column;
  align-items: center;
  min-height: 56px;
}

.header__logo {
  position: absolute;
  z-index: 2;
}

.main-nav {
  position: absolute;
  width: 100%;
}
.main-nav--closed .main-nav__toggle {
  top: 0;
  right: 0;
  left: auto;
}
.main-nav--closed .main-nav__toggle::before {
  content: "";
  position: absolute;
  top: 17px;
  right: 20px;
  width: 23px;
  height: 22px;
  background-image: url("/../img/icon-menu.svg");
  background-position: 0 0;
  background-repeat: no-repeat;
}
.main-nav--closed .main-nav__toggle:hover::before {
  opacity: 0.6;
}
.main-nav--closed .main-nav__toggle:active::before {
  opacity: 0.3;
}
.main-nav--closed .main-nav__list {
  display: none;
}
.main-nav--opened {
  z-index: 2;
}
.main-nav--opened .main-nav__list::after {
  content: "";
  position: absolute;
  bottom: 0;
  left: 0;
  z-index: 2;
  width: 100%;
  height: 7px;
  box-shadow: inset 0 0 7px -2px rgba(0, 0, 0, 0.2);
}
.main-nav--opened .main-nav__toggle::before {
  content: "";
  position: absolute;
  top: 17px;
  right: 20px;
  width: 23px;
  height: 22px;
  background-image: url("../img/icon-menu-close.svg");
  background-position: 0 0;
  background-repeat: no-repeat;
}
.main-nav--opened .main-nav__toggle:hover::before {
  opacity: 0.6;
}
.main-nav--opened .main-nav__toggle:active::before {
  opacity: 0.3;
}
.main-nav--nojs {
  position: static;
  margin-top: 102px;
}
.main-nav--nojs .main-nav__toggle {
  display: none;
}

.main-nav__toggle {
  position: absolute;
  top: 0;
  right: 0;
  z-index: 2;
  display: block;
  width: 63px;
  min-height: 56px;
  padding: 0;
  background-color: transparent;
  border: none;
  cursor: pointer;
}

@media (min-width: 768px) {
  .main-nav {
    position: relative;
    z-index: 1;
  }
  .main-nav--closed .main-nav__list {
    display: grid;
  }

  .main-nav__toggle {
    display: none;
  }
}
