.review-form {
  display: grid;
  grid-template-columns: 1fr;
  grid-template-rows: 1fr;
  border-top: 1px solid #e5e5e5;
}

.review-form__item {
  position: relative;
  width: 100%;
  margin: 0;
  padding: 20px 20px 39px;
  border: none;
  text-align: left;
}
.review-form__item::after {
  content: "";
  position: absolute;
  left: 0;
  bottom: 0;
  width: 100%;
  border-top: 1px solid #e5e5e5;
}
.review-form__item--review {
  padding-bottom: 56px;
}
.review-form__item--review::after {
  content: none;
}

.review-form__title {
  padding-top: 24px;
  font-size: 18px;
  font-weight: 700;
  line-height: 26px;
  text-align: center;
  color: #000000;
}

.review-form__label {
  margin-left: 16px;
  color: #333333;
}

.review-form__field {
  padding: 12px;
  width: 100%;
  border: 4px solid transparent;
  background-color: #f2f2f2;
  font-size: 18px;
  font-weight: 700;
  line-height: 26px;
  text-transform: initial;
  color: #000000;
  outline: none;
  cursor: pointer;
}
.review-form__field:not(:last-child) {
  margin-bottom: 16px;
}
.review-form__field::placeholder {
  color: rgba(0, 0, 0, 0.3);
}
.review-form__field:hover {
  background-color: #e5e5e5;
}
.review-form__field:focus {
  background-color: #ffffff;
  border: 4px solid #e5e5e5;
}
.review-form__field--text {
  margin-bottom: 0;
}

.review-form__label-radio {
  position: relative;
  padding-left: 64px;
  font-size: 18px;
  font-weight: 700;
  line-height: 26px;
  text-transform: initial;
  color: #000000;
  cursor: pointer;
}
.review-form__label-radio::before {
  content: "";
  position: absolute;
  bottom: auto;
  left: 0;
  width: 46px;
  height: 46px;
  border-radius: 30px;
  background-color: #f2f2f2;
  background-position: center;
  background-repeat: no-repeat;
  cursor: pointer;
}

.review-form__label-checkbox {
  position: relative;
  padding-left: 64px;
  font-size: 18px;
  font-weight: 700;
  line-height: 26px;
  text-transform: initial;
  color: #000000;
  cursor: pointer;
}
.review-form__label-checkbox::before {
  content: "";
  position: absolute;
  bottom: auto;
  left: 0;
  width: 46px;
  height: 46px;
  border-radius: 4px;
  background-color: #f2f2f2;
  background-position: center;
  background-repeat: no-repeat;
  cursor: pointer;
}

.review-form__required {
  margin: 0;
  margin-top: 3px;
  font-size: 12px;
  text-align: right;
  color: rgba(51, 51, 51, 0.6);
}

.review-form__action {
  display: flex;
  flex-direction: column;
  width: 100%;
  padding-right: 20px;
  padding-bottom: 22px;
  padding-left: 20px;
}

.review-form__radio:checked + .review-form__label-radio::before {
  background-image: url("../img/icon-radio.svg");
}

.review-form__checkbox:checked + .review-form__label-checkbox::before {
  background-image: url("../img/icon-check.svg");
}

@media (min-width: 768px) {
  .review-form {
    grid-template-columns: repeat(2, 1fr);
    border: none;
  }

  .review-form__item {
    width: 100%;
  }
  .review-form__item::after {
    content: none;
  }
  .review-form__item .review-form__title {
    position: relative;
    padding-top: 0;
    padding-bottom: 28px;
    width: 100%;
    font-size: 21px;
  }
  .review-form__item .review-form__title::before, .review-form__item .review-form__title::after {
    content: "";
    position: absolute;
    top: calc((100% - 28px) / 2);
    height: 1px;
    left: 0;
    width: calc((100% - 402px) / 2);
    background-color: #dbdbdb;
  }
  .review-form__item .review-form__title::after {
    left: auto;
    right: 0;
  }
  .review-form__item--name {
    padding: 0 29px 63px 0;
  }
  .review-form__item--contact {
    padding: 0 0 63px 29px;
  }
  .review-form__item--impression {
    grid-column: 1/3;
    padding: 0 0 48px 0;
  }
  .review-form__item--attractions {
    grid-column: 1/3;
    padding: 0 0 42px 0;
  }
  .review-form__item--review {
    grid-column: 1/3;
    padding: 0 0 48px 0;
  }
  .review-form__item--name .review-form__title::before, .review-form__item--name .review-form__title::after, .review-form__item--contact .review-form__title::before, .review-form__item--contact .review-form__title::after {
    content: none;
  }
  .review-form__item--contact::before {
    content: "";
    position: absolute;
    top: -61.5px;
    height: 338px;
    left: 0;
    width: 100%;
    border-left: 1px solid #dbdbdb;
  }

  .review-form__action {
    grid-column: 1/3;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    max-width: 768px;
    margin: 0 auto;
    padding-right: 0;
    padding-left: 0;
  }

  .review-form__field--text {
    display: block;
    max-width: 768px;
    height: 160px;
    margin: 0 auto;
  }

  .review-form__button {
    max-width: 324px;
    width: 100%;
  }

  .review-form__required {
    font-size: 14px;
  }
}
@media (min-width: 1200px) {
  .review-form__item--name {
    padding: 0 50px 71px 0;
  }
  .review-form__item--contact {
    padding: 0 0 71px 50px;
  }

  .review-form__button {
    max-width: 384px;
  }
